import './index.css';
import dva from 'dva'
import { persistEnhancer } from 'dva-model-persist';
import storage from 'dva-model-persist/lib/storage';
import goodsList from './models/product';
import cart from './models/cart';
// 
import App from './App';
// 
const app = dva();
// 
app.model(goodsList);
app.model(cart);
// 
app.use({
    extraEnhancers: [
        persistEnhancer({
            key: 'model',
            whitelist:['cart'],  //
            storage
        })
    ],
})
// 
app.router(() => <App />)
// 
app.start('#root')

